@import '../styles/utils.scss';

.SassComponent {
    display: flex;
    background: $oc-gray-2;
    justify-content: center;
    @include media ('<768px') {
        background: $oc-gray-9;
    }
    .box { // ÏùºÎ∞ò CSSÏóêÏÑúÎäî .SassComponentÏùò .boxÏôÄ ÎßàÏ∞¨Í∞ÄÏßÄ
        background:red;
        cursor: pointer;
        transition: all 0.3 ease-in;
        &.red {
            // .red ÌÅ¥ÎûòÏä§Í∞Ä .boxÏôÄ Ìï®Íªò ÏÇ¨Ïö©ÎêòÏóàÏùÑ Îïå
            background: $red;
            @include square(1);
        }
        &.orange {
            background: $orange;
            @include square(2);
        }
        &.yellow {
            background: $yellow;
            @include square(3);
        }
        &.green {
            background: $green;
            @include square(4);
        }
        &.blue {
            background: $blue;
            @include square(5);
        }
        &.indigo {
            background: $indigo;
            @include square(6);
        }
        &.violet {
            background: $violet;
            @include square(7);
        }
        &.hover {
            // .boxÏóê ÎßàÏö∞Ïä§Î•º Ïò¨Î†∏ÏùÑ Îïå
            background: black;
        }
    }
}